/* App.css */
body {
  font-family: "Caladea", serif;
  font-weight: 400;
  font-style: normal;
  margin: 0;
  padding: 0;
}

.title {
  padding-left: 30px;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(255, 255, 255);
  padding: 20px 0px;
  color: rgb(0, 0, 0);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 1000;
}

.navbar .title {
  font-size: 24px;
  font-weight: bold;
}

.navbar .menu {
  display: flex;
  gap: 20px;
  padding-right: 10px;
}

.navbar .menu a {
  color: rgb(0, 0, 0);
  text-decoration: none;
  font-size: 18px;
  padding: 10px 15px;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.navbar .menu a:hover {
  background-color: rgba(67, 61, 61, 0.2);
}

.main {
  text-align: center;
  padding: 80px 50px 50px; /* Adjust padding to account for fixed navbar */
}

.carousel {
  padding-top: 15px;
  position: relative;
  max-width: 75%;
  margin: auto;
  overflow: hidden;
}

.carousel-images {
  display: flex;
  transition: transform 0.5s ease-in-out;
}

.carousel img {
  width: 100%;
  flex-shrink: 0;
}

.carousel-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.6);
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  z-index: 10;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
}

.carousel-button .arrow {
  font-size: 20px;
  color: white;
}

.carousel-button.prev {
  left: 10px;
}

.carousel-button.next {
  right: 10px;
}

.carousel-button:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

.carousel-indicators {
  display: flex;
  justify-content: center;
  margin-top: 10px;
  gap: 5px;
}

.carousel-indicators .indicator {
  width: 10px;
  height: 10px;
  background-color: #ccc;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.3s;
}

.carousel-indicators .indicator.active {
  background-color: #333;
}

.news {
  padding: 20px;
  background-color: #f9f9f9;
}

.news h2 {
  text-align: center;
}

.news-list {
  list-style-type: none;
  padding: 0;
  max-width: 80%;
  margin: auto;
}

.news-list li {
  padding: 10px;
  border-bottom: 1px solid #ccc;
}

.footer {
  background-color: #222;
  color: white;
  text-align: left;
  padding: 10px 15px;
  font-size: 14px;
  margin-top: 30px;
}